.photoView{
    min-height: 87vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sectionTitle{
    text-align: center;
    padding: 1em;
    font-weight: 100;
    /* color: #f75b5a; */
}

.wallpaperSection{
    padding: 1em;
    padding-top: 0;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-evenly;
    align-items: stretch;
}

.wallpaper{
    max-width: 50vw;
    height: 70vh;
    max-height: 700px;
    border-radius: 5px;
}

.wallpaperInfo{
    /* background-color: #f1f1f1a1; */
    padding: 1em;
    border-radius: 5px;
    min-height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

@media(max-width: 800px){
    .wallpaperSection{
        flex-direction: column;
        align-items: center;
    }

    .wallpaper{
        max-width: 400px;
        width: 85vw;
    }

    .wallpaperInfo{
        margin-top: 1em;
    }
}

.description{
    font-size: 1.3em;
    font-weight: bold;
}

.likesAndViews{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 1em;
    border: 2px solid lightgrey;
    border-radius: 50px;
}

@media(max-width: 425px){
    .likesAndViews{
        border: none;
        border-radius: none;
    }
}

@media(max-width: 400px){
    .likesAndViews{
        flex-direction: column;
    }
}

.lw{
    padding-left: 1em;
    padding-right: 1em;
    display: flex;
    align-items: center;
    font-size: 1.2em;
    font-weight: bold;
}

.lw i{
    padding-right: 5px;
}

.downloads i{
    color: dodgerblue;
}

.views i{
    color: #503c8b;
}

.likes i{
    color: #f75b5a;
}

.dimensions{
    background-color: white;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
}

.dimensions .data{
    background-color: white;
    padding: 1em;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.dimensions .data i{
    font-weight: bold;
    padding: 5px;
}


.dimensions .data span{
    margin-left: 5px;
}

.author{
    padding-top: 1.5em;
}

.author a{
    color: #503c8b;
    font-weight: bold;
}

.downloadButton{
    margin-top: 1.5em;
    padding: 0.7em;
    padding-left: 2em;
    padding-right: 2em;
    border-radius: 0.3em;
    display: inline-flex;
    background-color: #64dd17;
    font-size: 1.1em;
    font-weight: bold;
    color: white;
}

.downloadButton:hover{
    background-color: #1faa00;
}

.downloadButton a{
    color: white !important;
    text-align: center;
}


